body {
  min-width: 320px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  min-height: 100vh;
  margin: 0;
  padding: 36px 80px 67px;
  box-sizing: border-box;
  background-image: url(../image/Pattern.svg);
  background-repeat: repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 1280px) {
  .main {
    padding: 36px 0 67px;
  }
}

.main__title {
  margin: 0 0 24px;
  padding: 0 20px;
  box-sizing: border-box;
  font-family: "Exo2.0";
  font-weight: 100;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #fff;
  text-shadow: 0px 1px 1px #000;
}

.main__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 80px;
}
@media screen and (max-width: 1280px) {
  .main__list {
    gap: 35px;
  }
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.article {
  width: 320px;
  height: 480px;
  padding-top: 21px;
  box-sizing: border-box;
  border: 4px solid #1698d9;
  background-color: #1698d9;
  border-radius: 12px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  -webkit-clip-path: polygon(0% 45px, 45px 0%, 100% 0%, 100% 0, 100% 100%, 100% 100%, 0 100%, 0 100%);
          clip-path: polygon(0% 45px, 45px 0%, 100% 0%, 100% 0, 100% 100%, 100% 100%, 0 100%, 0 100%);
  position: relative;
}
.article::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f2f2f2;
  border-radius: 12px;
  z-index: -1;
  -webkit-clip-path: polygon(0% 45px, 45px 0%, 100% 0%, 100% 0, 100% 100%, 100% 100%, 0 100%, 0 100%);
          clip-path: polygon(0% 45px, 45px 0%, 100% 0%, 100% 0, 100% 100%, 100% 100%, 0 100%, 0 100%);
}

.article_not-select:hover {
  border-color: #2ea8e6;
  background-color: #2ea8e6;
}
.article_not-select:hover .figcaption {
  background-color: #2ea8e6;
}

.article_select {
  border-color: #d91667;
  background-color: #d91667;
}

.article_select-hover:hover {
  border-color: #e52e7a;
  background-color: #e52e7a;
}
.article_select-hover:hover .figcaption {
  background-color: #e52e7a;
}

.article_disabled {
  cursor: default;
  border: 4px solid #b3b3b3;
  background-color: #b3b3b3;
}

.article__text {
  margin: 0 0 0 51px;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #666;
}

.article__text_hover {
  color: #e62e7a;
}

.article__text_disabled {
  color: #b3b3b3;
  opacity: 0.5;
}

.article__title {
  margin: 0 0 0 48px;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000;
}

.article__title_disabled {
  color: #b3b3b3;
  opacity: 0.5;
}

.article__subtitle {
  margin: 0 0 0 51px;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #000;
}

.article__subtitle_disabled {
  color: #b3b3b3;
  opacity: 0.5;
}

.article__list {
  list-style-type: none;
  margin: 15px 0 0 51px;
  padding: 0;
}

.article__list-item {
  margin: 0;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #666;
}

.article__list-item_disabled {
  color: #b3b3b3;
  opacity: 0.5;
}

.article__list-item_span {
  font-weight: 700;
}

.figure {
  margin: 0;
}

.figure__image {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-radius: 0 0 0 5px;
}

.figure__image_disabled {
  opacity: 0.5;
}

.figcaption {
  margin: 0;
  padding: 20px 0 8px;
  box-sizing: border-box;
  position: absolute;
  right: 16px;
  bottom: 16px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #1698d9;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
}

.figcaption_select {
  background-color: #d91667;
}

.figcaption_disabled {
  background-color: #b3b3b3;
}

.figcaption__text {
  margin: 0;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 400;
  font-size: 42px;
  line-height: 22px;
  text-align: center;
  color: #fff;
}

.figcaption__text-span {
  margin: 0;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 400;
  font-size: 21px;
  line-height: 22px;
  text-align: center;
  color: #fff;
}

.item__text {
  height: 15px;
  margin: 14px 0 0;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #fff;
}

.item__text_disabled {
  color: #ffff66;
}

.item__text-button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  color: #1698d9;
  text-decoration: none;
  background-color: transparent;
  border: none;
  padding: 0;
  border-bottom: 1px dashed #1698d9;
}
.item__text-button:hover {
  cursor: pointer;
  color: #22a7e9;
  border-bottom: 1px dashed #22a7e9;
}

.item__text-span {
  color: #1698d9;
}/*# sourceMappingURL=style.css.map */